<template>
  <section id="random">
    <p :class="{active:isActive}">{{something}}</p>
    <RandomButton @start="randomStart" @stop="randomStop"></RandomButton>
  </section>
</template>
    
<script>
import RandomButton from "./RandomButton.vue";

export default {
  data: function() {
    return {
      something: "",
      list: [
        "",
        "",
        "leetcode",
        "",
        "",
        "",
        ""
      ],
      clockId: 0,
      isActive: false
    };
  },
  methods: {
    randomStart: function() {
      this.isActive = false;
      var _this = this;
      this.clockId = setInterval(function() {
        _this.something = _this.list[Math.floor(Math.random() * 7)];
      }, 80);
    },
    randomStop: function() {
      this.isActive = true;
      clearInterval(this.clockId);
      const value = this.something;
      this.$store.commit("add", {
        itemText: value,
        itemId: 0,
        completed: false
      });
    }
  },
  components: {
    RandomButton
  }
};
</script>

<style scoped>
#random {
  text-align: center;
}
p {
  margin: 100px 0;
  color: #ffffff;
  font-size: 50px;
  font-weight: bold;
}
.active {
  color: #209975;
  font-size: 60px;
}
</style>

